"use client";

import { useState, useEffect } from "react";

export default function ReplaysPage() {
  const [division, setDivision] = useState("1");
  const [week, setWeek] = useState("1");
  const [file, setFile] = useState<File | null>(null);
  const [files, setFiles] = useState<string[]>([]);
  const [loading, setLoading] = useState(false);

  // Fetch list of files
  const fetchFiles = async () => {
    const res = await fetch(`/api/replays?division=${division}&week=${week}`);
    const data = await res.json();
    setFiles(data.files || []);
  };

  useEffect(() => {
    fetchFiles();
  }, [division, week]);

  // Handle file upload
  const handleUpload = async () => {
    if (!file) return;
    setLoading(true);

    const formData = new FormData();
    formData.append("file", file);
    formData.append("division", division);
    formData.append("week", week);

    const res = await fetch("/api/replays", {
      method: "POST",
      body: formData,
    });

    const data = await res.json();
    setLoading(false);

    if (data.error) {
      alert("Upload failed: " + data.error);
    } else {
      alert("Uploaded successfully!");
      setFile(null);
      fetchFiles(); // Refresh file list
    }
  };

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-4">Upload Replays</h1>

      <div className="mb-4">
        <label>Division: </label>
        <input value={division} onChange={e => setDivision(e.target.value)} />
        <label className="ml-4">Week: </label>
        <input value={week} onChange={e => setWeek(e.target.value)} />
      </div>

      <div className="mb-4">
        <input type="file" accept=".html" onChange={e => setFile(e.target.files?.[0] || null)} />
        <button
          onClick={handleUpload}
          disabled={!file || loading}
          className="ml-2 px-4 py-2 bg-blue-500 text-white rounded"
        >
          {loading ? "Uploading..." : "Upload"}
        </button>
      </div>

      <h2 className="text-xl font-semibold mb-2">Replays:</h2>
      <ul>
        {files.map(f => (
          <li key={f}>
            <a
              href={`https://YOUR_PROJECT_ID.supabase.co/storage/v1/object/public/replays/d${division}/w${week}/${f}`}
              target="_blank"
              rel="noopener noreferrer"
              className="text-blue-600 underline"
            >
              {f}
            </a>
          </li>
        ))}
      </ul>
    </div>
  );
}
